<template>
  <div class="service-admin">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-button type="primary" icon="el-icon-refresh" @click="add"></el-button>
    <div style="display: inline-block; width: 300px;">
      <el-input v-model="select_word" placeholder="" id="service-handle-input"></el-input>
    </div>
    <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
    <br>
    <br>
    <el-table :data="tableVisibleData" border style="width: 100%" ref="multipleTable" @selection-change="handleSelectionChange">
      <el-table-column prop="pk_SERVICE" label="Service ID" width="240"></el-table-column>
      <el-table-column prop="service_CODE" label=""></el-table-column>
      <el-table-column prop="owner_ORG" label=""></el-table-column>
      <el-table-column prop="owner" label=""></el-table-column>
      <el-table-column prop="type" label=""></el-table-column>
      <el-table-column label="" width="80" fixed="right" :resizable="false">
        <template scope="scope">
          <el-button size="mini" @click="handleDetail(scope.$index, scope.row)"></el-button>
          <!-- <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="10">
      </el-pagination>
    </div>
    <el-dialog title="" :visible.sync="detailVisible" :modal="false">
      <table border="0" class="detail-message" cellspacing="3" cellpadding="3">
        <tr>
          <th> key </th>
          <th> value </th>
        </tr>
        <tr v-for="(value, key) in detail">
          <td width="230">
            {{detail_CN[key]}}
          </td>
          <td>{{value}}</td>
        </tr>
      </table>
      <span slot="footer">
    		<el-button type="primary" @click="detailVisible = false"></el-button>
    	</span>
    </el-dialog>
    <el-dialog title="" :visible.sync="addVisible" :modal="false">
      <el-form :model="addData" label-width="80px" :inline="true" size="mini">
        <el-form-item label="">
          <el-input v-model="addData.SERVICE_CODE"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addData.SERVICE_VERSION"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="addData.OWNER_ORG">
            <el-option v-for="item in this.$store.state.platform" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="addData.OWNER">
            <el-option v-for="item in this.$store.state.system.MDXP" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="addData.TYPE">
            <el-option v-for="item in this.$store.state.serviceType" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="ENABLED">
          <el-select v-model="addData.ENABLED_FLAG">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div style="width: 450px">
            <el-input type="textarea" :rows="4" placeholder="" v-model="addData.REMARK" size="medium">
            </el-input>
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer">
				<el-button @click="addVisible = false"></el-button>
    		<el-button type="primary" @click="addItem"></el-button>
    	</span>
    </el-dialog>
    <el-dialog title="" :visible.sync="editVisible" :modal="false">
      <!-- <table border="0" cellspacing="0">
        <tr v-for="(value, key) in editData">
          <td width="230">
            <el-tag type="success">{{key}}</el-tag>
          </td>
          <td>
            <el-input v-model="editData[key]"></el-input>
          </td>
        </tr>
      </table> -->
      <el-form :model="addData" label-width="80px" :inline="true" size="mini">
        <el-form-item label="">
          <el-input v-model="editData.SERVICE_CODE"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editData.SERVICE_VERSION"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="editData.OWNER_ORG">
            <el-option v-for="item in this.$store.state.platform" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="editData.OWNER">
            <el-option v-for="item in this.$store.state.system.MDXP" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="editData.TYPE">
            <el-option v-for="item in this.$store.state.serviceType" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="ENABLED">
          <el-select v-model="editData.ENABLED_FLAG">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div style="width: 450px">
            <el-input type="textarea" :rows="4" placeholder="" v-model="editData.REMARK" size="medium">
            </el-input>
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer">
				<el-button @click="editVisible = false"></el-button>
    		<el-button type="primary" @click="editItem"></el-button>
    	</span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      api: this.$store.state.api + '/api/service',
      tableData: [],
      tableVisibleData: [],
      cur_page: 1,
      select_word: '',
      detail: {},
      detailVisible: false,
      addData: {
        SERVICE_CODE: "",
        SERVICE_VERSION: "1.0",
        OWNER_ORG: "",
        OWNER: "",
        TYPE: "",
        ENABLED_FLAG: "",
        REMARK: ""
      },
      addVisible: false,
      total: 0,
      editData: {
        SERVICE_CODE: "",
        SERVICE_VERSION: "",
        OWNER_ORG: "",
        OWNER: "",
        TYPE: "",
        ENABLED_FLAG: "",
        REMARK: ""
      },
      editVisible: false,
      editID: '',
      detail_CN: {
        'REMARK': '',
        'insert_TIMESTAMP': '',
        'last_UPDATE_TIMESTAMP': '',
        'pk_SERVICE': 'ID',
        'type': '',
        'owner_ORG': '',
        'owner': '',
        'service_CODE': '',
        'service_VERSION': '',
        'creator': '',
        'enabled_FLAG': '(10)',
        'remark': '',
        'modifier': ''
      }
    }
  },
  methods: {
    getData() {
      this.axios.get(this.api + '/select-all').then((response) => {
        this.tableData = response.data.data;
        console.log(this.tableData);
        this.setVisibleData();
      })
    },
    add() {
      // this.addVisible = true;
      const loading = this.$loading({
          lock: true,
          text: '',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // setTimeout(() => {
        //   loading.close();
        // }, 2000);
        this.axios.get(this.$store.state.api + '/sync-service').then((response) => {
          loading.close()
          this.$notify({
            title: '',
            message: ''+response.data.count,
            duration: 3000
          });
          this.getData()
          this.getPage()
        }).catch((error) => {
          loading.close()
          this.$notify({
            title: '',
            message: '',
            duration: 3000
          });
        })
    },
    search() {
      this.axios.get(this.api + '/selectservicebycondition', { params: { key: this.select_word.toUpperCase() } }).then((response) => {
        this.tableData = response.data.data;
        this.total = response.data.data.length;
        this.cur_page = 1;
        this.setVisibleData();
      })
    },
    handleSelectionChange() {

    },
    handleEdit(index, row) {
      this.editID = row.pk_SERVICE;
      this.editData['SERVICE_CODE'] = row.service_CODE;
      this.editData['SERVICE_VERSION'] = row.service_VERSION;
      this.editData['OWNER_ORG'] = row.owner_ORG;
      this.editData['OWNER'] = row.owner;
      this.editData['TYPE'] = row.type;
      this.editData['ENABLED_FLAG'] = row.enabled_FLAG;
      this.editData['REMARK'] = row.remark;
      this.editVisible = true;
    },
    handleDelete(index, row) {
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.axios({
            method: 'delete',
            url: this.api + '/delete',
            data: row
          }).then((response) => {
            if (response.data.code === 200) {
              this.getPage();
              this.getData();
              this.$message({
                message: '!',
                type: 'success'
              })
            }
            if (response.data.code === 400) {
              this.$message.error('');
            }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      })
    },
    handleDetail(index, row) {
      this.detail = row;
      this.detailVisible = true;
    },
    addItem() {
      this.axios.post(this.api + '/add', this.addData).then((response) => {
        if (response.data.code === 200) {
          this.getPage();
          this.getData();
          this.$message({
            message: '',
            type: 'success'
          });
          this.initData(this.addData);
        }
        if (response.data.code === 400) {
          this.$message.error('');
        }
      })
      this.getData();
      this.addVisible = false;
    },
    handleCurrentChange(val) {
      this.cur_page = val;
      //this.getData();
    },
    getPage() {
      this.axios.get(this.api + '/selectpageamount').then((response) => {
        this.total = response.data.data;
      })
    },
    setVisibleData() {
      var start = 10 * (this.cur_page - 1) + 1;
      var end = 10 * this.cur_page;
      var data = []
      for (let i = 0; i < this.tableData.length; i++) {
        if (i >= (start - 1) && i <= (end - 1)) {
          data.push(this.tableData[i])
        }
      }
      this.tableVisibleData = data;
    },
    editItem() {
      this.editData.PK_SERVICE = this.editID;
      this.axios.post(this.api + '/update', this.editData).then((response) => {
        if (response.data.code === 200) {
          this.getData();
          this.$message({ message: '', type: 'success' });
        }
        if (response.data.code === 400) {
          this.$message.error('');
        }
        this.editVisible = false;
      })
    },
    initData(data) {
      for (let i in data) {
        data[i] = '';
      }
    }
  },
  created: function() {
    this.getData();
    this.getPage();
  },
  watch: {
    cur_page: function() {
      console.log(this.cur_page);
      this.setVisibleData();
    }
  }
}

</script>
<style>
.crumbs {
  margin-bottom: 20px;
}

#service-handle-input {
  width: 300px;
  display: inline-block;
}

.pagination {
  margin: 20px 0;
  text-align: right;
}

.detail-message {
  border: 1px solid #ddd;
  margin: 0 auto;
}

.detail-message tr td{
  border-bottom: 1px solid #ddd;
}

.detail-message th{
  border-bottom: 1px solid black;
  text-align: left;
  background-color: #409EFF;
  color: #fff;
}

</style>



